




































import { Component, Vue } from "vue-property-decorator";
// import Square from "../components/square.vue";

@Component({
  components: {}
})
export default class Home extends Vue {
  slide = 0;
  sliding: any = null;
  onSlideStart(slide: any) {
    this.sliding = true;
  }
  onSlideEnd(slide: any) {
    this.sliding = false;
  }
}
