




























































import { Component, Prop, Vue } from "vue-property-decorator";
import axios from "axios";

@Component({})
export default class HelloWorld extends Vue {
  @Prop() private msg!: string;
  instaPosts = {};
  mounted() {
    axios
      .get("https://www.instagram.com/explore/tags/thenoobartist_tk/?__a=1")
      .then(
        (response: any) =>
          (this.instaPosts = response.data.graphql.hashtag.edge_hashtag_to_media.edges.filter(
            (post: any) => {
              return post.node.owner.id === "6032922270";
            }
          ))
      );
  }
}
